/* =======================================*/
/* ì»¤ë®¤ëí° > FAQ ì¡°í
/* (html/faq-list.html)
/* =======================================*/
.accordion-faq {
    > li {
        border-bottom: 1px solid #d2d4d6;
        &:first-child {
            border-top: 1px solid #d2d4d6;
        }
        [class*="link"] {
            position: relative;
            height: 60px;
            line-height: 60px;
            padding: 0 30px;
            [class^="title-"] {
                float: left;
                font-size: 16px;
                color: #333;
                vertical-align: top;
            }
            .title-type {
                font-weight: bold;
                width: 200px;
                &:before {
                    display: inline-block;
                    content: "Q.";
                    padding-right: 14px;
                }
            }
            .title-name {
                width: -webkit-calc( 100% - 240px );
                width: -moz-calc( 100% - 240px );
                width: calc( 100% - 240px );
                height: inherit;
                @include ellipsis(1);
            }
            &:before {
                position: absolute;
                top: 30px;
                right: 30px;
                width: 17px;
                height: 1px;
                background: #333;
                content: "";
            }
            &:after {
                position: absolute;
                top: 22px;
                right: 38px;
                width: 1px;
                height: 17px;
                background: #333;
                content: "";
                transform: rotate(-90deg);
            }
        }
        &.open {
            border-bottom: 0;
            &:first-child {
                border-top: 2px solid #45494d;
            }
            [class*="link"] {
                background: #faf6f5;
                border-bottom: 1px solid #d2d4d6;
                [class^="title-"] {
                    color: #003761;
                }
                &:after {
                    transform: rotate(0deg);
                    transition: transform 0.25s ease;
                }
            }
            [class*="sub"] {
                border-bottom: 1px solid #45494d;
            }
        }
    }
    [class*="sub"] {
        position: relative;
        padding: 16px 30px 16px 60px;
        &:before {
            position: absolute;
            left: 30px;
            display: inline-block;
            content: "A.";
            font-weight: bold;
        }
        p {
            line-height: 1.6;
        }
    }
}

