[class^="table-type-"] {
    .table-download {
        position: relative;
        cursor: pointer;
        outline: none;
        [class^="icon-"] {
            display: inline-block;
            width: 14px;
            height: 16px;
            padding-left: 0;
            margin: 0 3px;
            vertical-align: -2px;
            &:before {
                top: 0;
            }
        }
        /* ë§íì  ë¤ì´ë¡ë ìì ¯ ì ì© */
        .download-file {
            @include w-center ($position: absolute);
            top: 43px;
            width: 250px;
            text-align: left;
            margin-left: -5px;
            letter-spacing: -0.03em;
            padding: 16px 20px;
            border: 1px solid #8aa3b7;
            background: #fff;
            box-shadow: 0 3px 10px rgba(0, 0, 0, 0.3);
            visibility: hidden;
            z-index: 9999;
            &:after {
                @include w-center ($position: absolute);
                top: -5px;
                width: 8px;
                height: 8px;
                border: 1px solid #8aa3b7;
                border-bottom: 0;
                border-right: 0;
                background: #fff;
                transform: rotate(45deg);
                content: "";
            }
            &:before {
                display: inline-block;
                position: absolute;
                top: -43px;
                left: 0;
                right: 0;
                height: 43px;
                content: "";
            }
            strong {
                display: block;
                margin-bottom: 6px;
            }
            a {
                display: block;
                position: relative;
                padding-left: 10px;
                padding-bottom: 6px;
                &:focus, &:hover {
                    color: #002847;
                    text-decoration: underline;
                }
                &:last-child {
                    padding-bottom: 0;
                }
                &:after {
                    display: inline-block;
                    position: absolute;
                    top: -3px;
                    left: 0;
                    content: "-";
                }
            }
            .btn-download-close {
                position: absolute;
                top: 15px;
                right: 15px;
                width: 20px;
                height: 20px;
                text-indent: -9999px;
                &:before {
                    position: absolute;
                    top: 9px;
                    left: 0;
                    display: block;
                    width: 20px;
                    height: 1px;
                    background: #8aa3b7;
                    transform: rotate(45deg);
                    content: "";
                }
                &:after {
                    position: absolute;
                    top: 9px;
                    left: 0;
                    display: block;
                    width: 20px;
                    height: 1px;
                    background: #8aa3b7;
                    transform: rotate(-45deg);
                    content: "";
                }
            }
        }
        &:focus, &.show-widget {
            .download-file {
                visibility: inherit;
            }
        }
    }
}
/* Content List Table */
.table-type-01 {
    width: 100%;
    thead {
        border-top: 2px solid #45494d;
        th {
            border-right: 1px solid #d1d1d1;
            border-bottom: 1px solid #c2c2c2;
        }
    }
    tbody {
        th, td {
            border-right: 1px solid #d1d1d1;
            border-bottom: 1px solid #d1d1d1;
        }
    }
    tr {
        th {
            background: #f0f3f5;
            color: #2e3a42;
        }
        th, td {
            height: 38px;
            text-align: center;
            padding: 0 10px;
            /* ì ëª© - íì ë§í¬ */
            .view-link {
                @include ellipsis(1);
                &:focus, &:hover {
                    color: #002847;
                    text-decoration: underline;
                }
            }
            /* ì ëª© - ì ì²´ ì¡°íì */
            .total-number {
                color: #003761;
                font-weight: bold;
                margin-left: 4px;
                text-decoration: none;
            }
            &:last-child {
                border-right: 0;
            }
        }
    }
}
/* Popup List Table */
.table-type-02 {
    width: 100%;
    tr {
        th, td {
            height: 42px;
            text-align: left;
            border: 1px solid #d1d1d1;
            .view-link {
                @include ellipsis(1);
            }
        }
        th {
            background: #f0f3f5;
            color: #2e3a42;
            border-right: 0;
            padding: 0 20px;
        }
        td {
            padding: 6px 10px;
        }
    }
}
/* ul, li íì´ë¸ íí */
.table-li {
    display: table;
    width: 100%;
    border: 1px solid #d1d1d1;
    li {
        display: table-cell;
        vertical-align: middle;
        min-height: 42px;
        padding: 10px;
        border-right: 1px solid #d1d1d1;
        &.title {
            font-weight: bold;
            color: #2e3a42;
            background: #f0f3f5;
            height: 42px;
            padding: 0 20px;
        }
        &:last-child {
            border-right: 0;
        }
    }
}
/* dl, dt, dd íì´ë¸ íí */
.table-dl {
    width: 100%;
    display: table;
    border: 1px solid #d1d1d1;
    dt, dd {
        display: table-cell;
        height: 42px;
        line-height: 42px;
    }
    dt {
        font-weight: bold;
        color: #2e3a42;
        padding: 0 20px;
        background: #f0f3f5;
        border-right: 1px solid #d1d1d1;
    }
    dd {
        padding: 0 10px;
    }
}
.table-dt-dd {
    width: 100%;
    border: 1px solid #d1d1d1;
    dt, dd {
        border-bottom: 1px solid #d1d1d1;
        &:last-child {
            border-bottom: 0;
        }
    }
    dt {
        height: 42px;
        line-height: 42px;
        font-weight: bold;
        text-align: center;
        color: #2e3a42;
        padding: 0 10px;
        background: #f0f3f5;
    }
    dd {
        padding: 10px;
    }
}
/* íì´ë¸ ë²í¼ ìì­ */
.table-btn-set {
    margin-top: 30px;
    text-align: center;
    [class^="btn-"] {
        margin-right: 3px;
        &:last-child {
            margin-right: 0;
        }
    }
    &[class*="in-"] {
        margin-top: 10px;
    }
    &.in-left {
        text-align: left;
    }
    &.in-right {
        text-align: right;
    }
}